import Image from "next/image";
import React from "react";
import Title from "./ui/Title";
import { useAboutQuery } from "@/hooks/queries/useAboutQuery";
import Link from "next/link";
import Button from "./Button";
import parse from "html-react-parser";
import Loader from "./Loader";
import Error from "./Error";

const OmOsSection = () => {
  const { data, isError, isLoading } = useAboutQuery();

  return (
    <section className="flex justify-between mt-60">
      <figure className="w-[47%]">
        <Image src={"/om-os.jpg"} width={1000} height={500} alt="Billede fra rummet for Om Os" />
      </figure>
      <section className="w-[45%] flex flex-col gap-10 my-auto">
        {isLoading && <Loader />}
        {isError && <Error />}
        {data && (
          <>
            <Title className="text-5xl font-bold" text="Lidt om os" />

            <div className="flex flex-wrap gap-y-3 justify-end">
              <Title className="w-full text-lightseagreen uppercase" text={data.title} />
              <div className="w-1/2 h-1 bg-lightseagreen" />
              <div className="w-1/2 h-[0.175rem] mt-auto bg-neutral-200" />
            </div>

            <div className="text-lg">{parse(data.content)}</div>

            <Link href={"/kontakt"}>
              <Button className="px-16">Kontakt os</Button>
            </Link>
          </>
        )}
      </section>
    </section>
  );
};

export default OmOsSection;
